      style="undefined;table-layout: fixed; width: 100%; margin-bottom: 50px;"
      ref="tablik"
    >
      <thead>
        <tr>
          <th class="tg-y21g"> </th>
          <th class="tg-y21g">- </th>
          <th class="tg-y21g">- </th>
          <th class="tg-y21g">- </th>
          <th class="tg-y21g"></th>
          <th class="tg-y21g"></th>
          <th class="tg-y21g" v-if="this.tablesList.name !== ''">
            
          </th>
          <th class="tg-y21g"></th>
          <th class="tg-y21g"> .</th>
          <th class="tg-y21g"> .</th>
          <th class="tg-y21g"> </th>
          <th class="tg-y21g"> .</th>
          <th class="tg-y21g">   </th>
          <th class="tg-y21g" v-if="this.tablesList.systemTax == 'nds20'">
            
          </th>
          <th class="tg-y21g" v-if="this.tablesList.systemTax == 'nds20'">
             <br /> 
          </th>
          <th class="tg-y21g">    </th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="tg-0lax" rowspan="2">
            <slot>{{ this.tablesList.name }}</slot>
          </td>
          <td class="tg-0lax" >1</td>
          <td class="tg-0lax" >
            <slot>{{ this.tablesList.hours }}</slot>
          </td>
          <td class="tg-0lax" >
            <slot>{{ this.tablesList.days }}</slot>
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.fot) }}</slot> 
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.ndfl) }}</slot> 
          </td>
          <td
            class="tg-0lax"
            v-if="this.tablesList.name !== ''"
            
          ><slot>{{ formated(this.tablesList.ffomc + this.tablesList.ffomc2) }}</slot> </td>
          <td class="tg-0lax" ><slot>{{ formated(this.tablesList.pfrFix + this.tablesList.pfrRemainder) }}</slot> </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.fssInsurance) }}</slot> 
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.fssTrauma) }}</slot> 
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.taxBase) }}</slot> 
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.outstaffing) }}</slot> 
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumWithoutNdc) }}</slot> 
          </td>
          <td class="tg-0lax" v-if="this.tablesList.systemTax == 'nds20'">
            20%
          </td>
          <td
            class="tg-0lax"
            
            v-if="this.tablesList.systemTax == 'nds20'"
          >
            <slot>{{ formated(this.tablesList.sumAndNdc) }}</slot> 
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.salaries) }}</slot> 
          </td>
        </tr>
<!-- 
        <tr>
          <td
            class="tg-0lax"
            rowspan="4"
            v-if="this.tablesList.systemTax == 'nds20'"
          >
            <slot>{{ formated(this.tablesList.ndc) }}</slot> 
          </td>
        </tr> -->

        <tr>
          <td class="tg-0lax" >
            <slot>{{ this.tablesList.countEmpol }}</slot>
          </td>
          <td class="tg-0lax" >8</td>
          <td class="tg-0lax" >22</td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumFot) }}</slot> 
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumNdfl) }}</slot>
            
          </td>
          <td
            class="tg-0lax"
            v-if="this.tablesList.name !== ''"
            
          ><slot>{{ formated(this.tablesList.sumFfomc + this.tablesList.sumFfomc2) }}</slot> </td>
          <td
            class="tg-0lax" 
          ><slot>{{ formated(this.tablesList.sumPfrFix + this.tablesList.sumPfrRemainder) }}</slot> </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumFssInsurance) }}</slot>
            
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumFssTrauma) }}</slot>
            
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumTaxBase) }}</slot>
            
          </td>
          <td class="tg-0lax" >
            <slot>{{
              formated(
                Number(this.tablesList.outstaffing) *
                  Number(this.tablesList.countEmpol)
              )
            }}</slot>
            
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumSumWithoutNdc) }}</slot>
            
          </td>
          <td class="tg-0lax" v-if="this.tablesList.systemTax == 'nds20'">
            20%
          </td>
          <td
            class="tg-0lax"
            
            v-if="this.tablesList.systemTax == 'nds20'"
          >
            <slot>{{ formated(this.tablesList.sumSumAndNdc) }}</slot>
            
          </td>
          <td class="tg-0lax" >
            <slot>{{ formated(this.tablesList.sumSalaries) }}</slot>
            
          </td>
        </tr>

        <!-- <tr>
          <td
            class="tg-0lax"
            rowspan="4"
            v-if="this.tablesList.systemTax == 'nds20'"
          >
            <slot>{{ formated(this.tablesList.sumNdc) }}</slot>
            
          </td>
        </tr> -->

      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "table-vue",
  props: ["tablesList"],
  data() {
    return {
      fssProc: "1.80%",
    };
  },

  methods: {
    checkFssProc() {
      if (this.tablesList.name !== "") {
        this.fssProc = "2.90%";
      }
      if (this.tablesList.name == "") {
        this.fssProc = "1.80%";
      }
    },
    getInnerHTML(table) {
      return table;
    },
    formated(value) {
      let formatter = new Intl.NumberFormat("ru", {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
      });
      return formatter.format(value);
    },
  },
  mounted() {
    this.$nextTick(function () {
      this.checkFssProc();
    });
  },
};
</script>

<style>
.tg {
  border-collapse: collapse;
  border-spacing: 0;
}
.tg td {
  border-color: black;
  border-style: solid;
  border-width: 1px;
  line-height: 1;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 14px;
  overflow: hidden;
  padding: 20px 10px !important;
  word-break: normal;
}
.tg th {
  border-color: black;
  border-style: solid;
  border-width: 1px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  font-weight: normal;
  overflow: hidden;
  padding: 15px 10px !important;
  word-break: normal;
}
.tg .tg-y21g {
  background-color: #3d5afe;
  background-color: #a2cf6e;
  text-align: center;
  vertical-align: center;
}
.tg .tg-0lax {
  text-align: center;
  vertical-align: center;
}
</style>
