<template>
  <label class="input_calc">
    <Transition name="slide-fade">
      <span class="error error-mrot" v-if="this.rf <= 23499 && !isDisableFotRf"
        >,     . <br /><br />  
        ,        .
        <br /><br />   ,   
        .</span
      ></Transition
    >
    <input
      type="number"
      min="0"
      v-model="rf"
      @input="updateValue"
      :disabled="isDisableFotRf"
      required
      placeholder="0"
      class="wage-level__patent"
    />
    <span></span>
  </label>
</template>

<script>
export default {
  props: {
    isDisableFotRf: {
      type: Boolean,
    },
  },

  data() {
    return {
      rf: null,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updateFotRf", this.rf);
    },
    resetValue() {
      this.rf = 23500;
      this.$emit("updateFotRf", this.rf);
    },
  },
};
</script>

<style></style>
