<template>
  <div class="left_block">
    <div class="left_block__inner">
      <h3></h3>
      <div class="checkbox_wrapper amound">
        <label class="input_calc">
          <input
            type="number"
            min="0"
            v-model="outsource"
            @input="updateValue"
            required
            placeholder="0"
            class="sum-staff__eaeu"
          />
          <span class="error"></span>
        </label>
      </div>
      <div class="important_info"> </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "OutstaffingVue",

  data() {
    return {
      outsource: 0,
    };
  },

  methods: {
    updateValue() {
      this.$emit("upOutsource", this.outsource);
    },
    resetValue() {
      this.outsource = 0;
      this.$emit("upOutsource", this.outsource);
    },
  },
};
</script>

