<template>
  <label class="input_calc">
    <Transition name="slide-fade">
      <span
        class="error error-mrot"
        v-if="this.eaeu <= 23499 && !isDisableFotEaeu"
        >,     . <br /><br />  
        ,        .
        <br /><br />   ,    .
      </span>
    </Transition>
    <input
      type="number"
      min="0"
      v-model="eaeu"
      @input="updateValue"
      :disabled="isDisableFotEaeu"
      required
      placeholder="0"
      class="wage-level__patent"
    />
    <span></span>
  </label>
</template>

<script>
export default {
  props: {
    isDisableFotEaeu: {
      type: Boolean,
    },
  },

  data() {
    return {
      eaeu: null,
    };
  },

  methods: {
    updateValue() {
      this.$emit("updateFotEaeu", this.eaeu);
    },
    resetValue() {
      this.eaeu = 23500;
      this.$emit("updateFotEaeu", this.eaeu);
    },
  },
};
</script>

<style></style>
