<template>
  <table class="tg">
    <thead>
      <tr>
        <th class="tg-bobw" colspan="4">
          <span style="font-weight: bold"> :</span>
        </th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td class="tg-a08a">
          <span style="background-color: #f8cbad">  </span>
        </td>
        <td class="tg-s4mc" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal; background-color: #f8cbad"
            ></span
          >
        </td>
        <td class="tg-s4mc" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal; background-color: #f8cbad"
            >
          </span>
        </td>
        <td class="tg-s4mc" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal; background-color: #f8cbad">
            </span
          >
        </td>
      </tr>
      <tr>
        <td class="tg-j6zm">
          <span style="font-weight: bold"> </span>
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListPatent.outstaffing) }}</slot> </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListEaeu.outstaffing) }}</slot> </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListRf.outstaffing) }}</slot> </span
          >
        </td>
      </tr>
      <tr>
        <td class="tg-j6zm">
          <span style="font-weight: bold"> ()</span>
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListPatent.fot) }}</slot> </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListEaeu.fot) }}</slot> </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListRf.fot) }}</slot> </span
          >
        </td>
      </tr>
      <tr>
        <td class="tg-j6zm">
          <span style="font-weight: bold"
            >   </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListPatent.outstaffing * 0.1) }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListEaeu.outstaffing * 0.1) }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal"
            ><slot>{{ formated(tableListRf.outstaffing * 0.1) }}</slot> </span
          >
        </td>
      </tr>
      <tr>
        <td class="tg-j6zm">
          <span style="font-weight: bold"
            >   </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListPatent.outstaffing * 0.1 * tableListPatent.countEmpol
              )
            }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListEaeu.outstaffing * 0.1 * tableListEaeu.countEmpol
              )
            }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(tableListRf.outstaffing * 0.1 * tableListRf.countEmpol)
            }}</slot>
            </span
          >
        </td>
      </tr>
      <tr>
        <td class="tg-j6zm">
          <span style="font-weight: bold"
            >-    </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal"
            ><slot>{{
              tableListPatent.hours * tableListPatent.days
            }}</slot></span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal"
            ><slot>{{ tableListEaeu.hours * tableListEaeu.days }}</slot></span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal"
            ><slot>{{ tableListRf.hours * tableListRf.days }}</slot></span
          >
        </td>
      </tr>
      <tr>
        <td class="tg-j6zm">
          <span style="font-weight: bold">  </span>
        </td>
        <td
          class="tg-2b7s"
          :class="{ bgErr: this.emloyTdMin.patent }"
          v-if="Object.keys(tableListPatent).length"
        >
          <span style="font-weight: normal">{{ this.emloyTd.patent }} </span>
        </td>
        <td
          class="tg-2b7s"
          :class="{ bgErr: this.emloyTdMin.eaeu }"
          v-if="Object.keys(tableListEaeu).length"
        >
          <span style="font-weight: normal">{{ this.emloyTd.eaeu }} </span>
        </td>
        <td
          class="tg-2b7s"
          :class="{ bgErr: this.emloyTdMin.rf }"
          v-if="Object.keys(tableListRf).length"
        >
          <span style="font-weight: normal">{{ this.emloyTd.rf }} </span>
        </td>
      </tr>
      <tr>
        <td class="tg-j6zm">
          <span style="font-weight: bold"
            >    ( )</span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListPatent.sumWithoutNdc /
                  (tableListPatent.hours * tableListPatent.days)
              )
            }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListEaeu.sumWithoutNdc /
                  (tableListEaeu.hours * tableListEaeu.days)
              )
            }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListRf.sumWithoutNdc /
                  (tableListRf.hours * tableListRf.days)
              )
            }}</slot>
            </span
          >
        </td>
      </tr>
      <tr v-if="this.systemTax == 'nds20'">
        <td class="tg-j6zm">
          <span style="font-weight: bold"
            >    ( )</span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListPatent).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListPatent.sumAndNdc /
                  (tableListPatent.hours * tableListPatent.days)
              )
            }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListEaeu).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListEaeu.sumAndNdc /
                  (tableListEaeu.hours * tableListEaeu.days)
              )
            }}</slot>
            </span
          >
        </td>
        <td class="tg-2b7s" v-if="Object.keys(tableListRf).length">
          <span style="font-weight: normal"
            ><slot>{{
              formated(
                tableListRf.sumAndNdc / (tableListRf.hours * tableListRf.days)
              )
            }}</slot>
            </span
          >
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  props: ["tablesList"],
  data() {
    return {
      CONSTS: {
        FOT_FIX: 13890,
        NDFL: 0.13,
        NDS: 0.2,
        PFR_FIX: 0.22,
        PFR_REMAINDER: 0.1,
        FSS_INSURANCE: 0.018,
        FSS_OTHER: 0.029,
        FFOMC_FIX: 0.051,
        FFOMC_REMAINDER: 0.05,
      },
      tableListPatent: {},
      tableListEaeu: {},
      tableListRf: {},
      reglament: 0,
      reglamentRemain: 0,
      ffomc: 0,
      ffomcRemained: 0,
      fssC: 0,
      fssTrauma: 0,
      tax: 0,
      sumWithoutNdc: 0,
      ndc: 0,
      sumAndNdc: 0,
      emloyTd: {
        patent: 136,
        eaeu: 136,
        rf: 136,
      },
      emloyTdMin: {
        patent: false,
        eaeu: false,
        rf: false,
      },
      systemTax: null,
    };
  },

  methods: {
    calc() {
      this.emloyTd.patent = (
        this.tableListPatent.fot /
        (this.tableListPatent.hours * this.tableListPatent.days)
      ).toFixed(2);
      this.emloyTd.eaeu = (
        this.tableListEaeu.fot /
        (this.tableListEaeu.hours * this.tableListEaeu.days)
      ).toFixed(2);
      this.emloyTd.rf = (
        this.tableListRf.fot /
        (this.tableListRf.hours * this.tableListRf.days)
      ).toFixed(2);

      if (Number(this.emloyTd.patent) <= 136) {
        this.emloyTdMin.patent = true;
        this.emloyTd.patent = 136;
      } else if (Number(this.emloyTd.patent) > 136) {
        this.emloyTdMin.patent = false;
      }
      if (this.emloyTd.eaeu <= 136) {
        this.emloyTd.eaeu = 136;
        this.emloyTdMin.eaeu = true;
      } else {
        this.emloyTdMin.eaeu = false;
      }
      if (this.emloyTd.rf <= 136) {
        this.emloyTdMin.rf = true;
        this.emloyTd.rf = 136;
      } else {
        this.emloyTdMin.rf = false;
      }
    },
    checkObj() {
      this.tableListPatent = {};
      this.tableListEaeu = {};
      this.tableListRf = {};
      this.tablesList.forEach((obj) => {
        switch (obj.name) {
          case "":
            this.tableListPatent = obj;
            break;
          case "":
            this.tableListEaeu = obj;
            break;
          case "":
            this.tableListRf = obj;
            break;
        }
      });
    },
    formated(value) {
      let formatter = new Intl.NumberFormat("ru", {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
      });
      return formatter.format(value);
    },
    showSystemTax() {
      if (Object.keys(this.tableListPatent).length) {
        this.systemTax = this.tablesList[0].systemTax;
      }
      if (Object.keys(this.tableListEaeu).length) {
        this.systemTax = this.tablesList[1].systemTax;
      }
      if (Object.keys(this.tableListRf).length) {
        this.systemTax = this.tablesList[2].systemTax;
      }
    },
  },
  mounted() {
    this.$nextTick(function () {
      this.checkObj();
      this.showSystemTax();
      this.calc();
    });
  },
};
</script>

<style>
.tg {
  border-collapse: collapse;
  border-spacing: 0;
}
.tg td {
  border-color: black;
  border-style: solid;
  border-width: 1px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  overflow: hidden;
  padding: 10px 5px;
  word-break: normal;
  text-align: left;
}
.tg th {
  border-color: black;
  border-style: solid;
  border-width: 1px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  overflow: hidden;
  padding: 10px 5px;
  word-break: normal;
}
.tg .tg-2b7s {
  text-align: center;
  vertical-align: bottom;
}
.tg .tg-bobw {
  font-weight: bold;
  text-align: center;
  vertical-align: bottom;
}
.tg .tg-s4mc {
  background-color: #f8cbad;
  text-align: center;
  vertical-align: bottom;
}
.tg .tg-a08a {
  background-color: #f8cbad;
  text-align: left;
  vertical-align: bottom;
}
.tg .tg-j6zm {
  font-weight: bold;
  text-align: left;
  vertical-align: bottom;
}
</style>
