<script>
import Caclulate from "../../../services/calculate";

export default {
  props: { tableInfo: Object, outsource: Number, fss: Number },
  data() {
    return {
      CONSTS: {
        FOT_FIX: 13890,
        NDFL: 0.13,
        NDS: 0.2,
        PFR_FIX: 0.22,
        PFR_REMAINDER: 0.1,
        FSS_INSURANCE: 0.018,
        FSS_OTHER: 0.029,
        FFOMC_FIX: 0.051,
        FFOMC_REMAINDER: 0.05,
      },
      fssProc: 1.8,
      hoursInMonth: 0,
      reglament: 0,
      reglamentRemain: 0,
      ndfl: 0,
      fot: 0,
      manager: 0,
      costHoursEmploy: 0,
      pfr: 0,
      ffomc: 0,
      ffomcRemained: 0,
      pfrRemained: 0,
      fssC: 0,
      fssTrauma: 0,
      tax: 0,
      sumWithoutNdc: 0,
      ndc: 0,
      sumAndNdc: 0,
    };
  },

  methods: {
    formated(value) {
      let formatter = new Intl.NumberFormat("ru", {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
      });
      return formatter.format(value);
    },
    hoursInMounth() {
      this.hoursInMonth = Caclulate.hoursInMonth(
        this.tableInfo.hours,
        this.tableInfo.days
      );
    },
    calcReglament() {
      this.reglament = Caclulate.reglament(this.tableInfo.fot);
    },
    calcReglamentRemain() {
      this.reglamentRemain = Caclulate.reglamentRemain(
        this.reglament,
        this.CONSTS.FOT_FIX
      );
    },
    calcNdfl() {
      this.ndfl = Caclulate.calcNdfl(this.reglament, this.CONSTS.NDFL);
    },
    calcFotFull() {
      this.fot = Caclulate.calcFotFull(this.tableInfo.fot, this.ndfl);
    },
    calcManager() {
      this.manager = Caclulate.calcManager(this.fot, this.reglament);
    },
    calcCostHoursEmploy() {
      this.costHoursEmploy = Caclulate.costHoursEmploy(
        this.fot,
        this.hoursInMonth
      );
    },
    calcPfr() {
      this.pfr = Caclulate.calcPfr(this.CONSTS.FOT_FIX, this.CONSTS.PFR_FIX);

      this.pfrRemained = Caclulate.calcPfr(
        this.reglamentRemain,
        this.CONSTS.PFR_REMAINDER
      );
    },
    calcFfomc() {
      this.ffomc = Caclulate.calcFfomc(
        this.CONSTS.FOT_FIX,
        this.CONSTS.FFOMC_FIX
      );
      this.ffomcRemained = Caclulate.calcFfomc(
        this.reglamentRemain,
        this.CONSTS.FFOMC_REMAINDER
      );
    },
    calcFss() {
      this.fssC = Caclulate.calcFss(this.CONSTS.FOT_FIX, this.tableInfo.name);
    },
    calcFssTrauma() {
      this.fssTrauma = Caclulate.calcFssTrauma(this.reglament, this.fss);
    },
    calcTax() {
      this.tax = Caclulate.calcTax(
        this.pfr,
        this.pfrRemained,
        this.fssC,
        this.fssTrauma,
        this.ffomc,
        this.ffomcRemained,
        this.tableInfo.name
      );
    },
    calcSumWithoutNdc() {
      this.sumWithoutNdc = Caclulate.calcSumWithoutNdcCom(
        this.fot,
        this.tax,
        this.outsource * 10,
        this.manager * 0.15
      );
    },
    calcNdc() {
      this.ndc = Caclulate.calcNdc(this.sumWithoutNdc);
    },
    calcSumAndNdc() {
      this.sumAndNdc = Caclulate.calcSumAndNdc(this.sumWithoutNdc, this.ndc);
    },
    calcSumEmploy(data) {
      return Number(Number(this.tableInfo.countEmpol) * Number(data));
    },
    calcAll() {
      this.hoursInMounth();
      this.calcReglament();
      this.calcReglamentRemain();
      this.calcNdfl();
      this.calcFotFull();
      this.calcManager();
      this.calcCostHoursEmploy();
      this.calcPfr();
      this.calcFfomc();
      this.calcFss();
      this.calcFssTrauma();
      this.calcTax();
      this.calcSumWithoutNdc();
      this.calcNdc();
      this.calcSumAndNdc();
    },
  },
  mounted() {
    this.$nextTick(function () {
      this.calcAll();
      if (this.tableInfo.name !== "") {
        this.fssProc = 2.9;
      }
    });
  },
};
</script>

